.cb-mapembed{width:100%;background:var(--ceiba-color-bg);border:1px solid color-mix(in oklab,var(--ceiba-color-ink) 12%,transparent);border-radius:var(--ceiba-radius-md);box-shadow:var(--ceiba-shadow-1);padding:var(--ceiba-space-xs)}
.cb-mapembed iframe{display:block;width:100%;height:100%;aspect-ratio:16/9;border:0;border-radius:var(--ceiba-radius-sm)}
.cb-mapembed__placeholder{display:flex;align-items:center;justify-content:center;min-height:240px;color:var(--ceiba-color-ink)}


.cb-mapembed{position:relative}
.cb-mapembed__overlay{position:absolute;inset:0;display:flex;align-items:center;justify-content:center;font-size:.85rem;color:var(--ceiba-color-ink);pointer-events:none;background:linear-gradient(to bottom, rgba(255,255,255,.6), rgba(255,255,255,.6))}
